export default class WebRTCTools {


static  getMyDivices(strForVoiceOrVideo,video,audio){

    switch(strForVoiceOrVideo){
        case'v':
        navigator.mediaDevices.getUserMedia({video:video||true,audio:false}).then(stream=>{return stream}).catch(err=>{return err});
        break;
        case'a':
        navigator.mediaDevices.getUserMedia({video:false,audio:audio||true}).then(stream=>{return stream}).catch(err=>{return err});
        break;
        case'av':
        case'va':
        navigator.mediaDevices.getUserMedia({video:video||true,audio:audio||true}).then(stream=>{return stream}).catch(err=>{return err});
        break;

        default: return new Error('you need to enter a string that contain a "v" for video || "a" for audio || "av" "va" for audio and video \n you can send a obj for how you want the video or the audio be ')

    }

}


}


